body {
	background-color: #b0b0b0;
	margin: 0;
}

.col{
	height: 400px;
}
.picture{
	background-image: url('https://picsum.photos/700/500');
	background-position: center;
	background-repeat: no-repeat;
}
.testimonial-image{
	img{
		width: 100%;
		border-radius: 50%;
	}
	
}

.testimonials{
	margin: 20px;
}

section{
	width: 100%;
	height: 400px;
	display: flex;
	article{
		width: 50%;
		overflow: hidden;

		img{
			width: 100%;
		}
	}
	.contentContainer{
		display: flex;
		justify-content: center;
		align-items: center;
		div{
			padding: 50px;
			width: 80%;
		}
	}
}